import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RolesAndPermissionsListComponent } from './roles-and-permissions-list/roles-and-permissions-list.component';
import { AddRoleComponent } from './add-role/add-role.component';
import { SetPermissionsComponent } from './set-permissions/set-permissions.component';




const routes: Routes = [
	{ path: '', redirectTo: 'roles-permissions-list', pathMatch: 'full'},
	{ path: 'roles-permissions-list', component: RolesAndPermissionsListComponent},
	{ path: 'add-roles-permissions', component: AddRoleComponent},
	{ path: 'edit-roles-permissions/:id', component: AddRoleComponent},
	{ path: 'set-permission', component: SetPermissionsComponent},
	{ path: '**', redirectTo: 'roles-permissions-list', pathMatch: 'full'}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RolesAndPermissionsRouting { }
