import { Component, OnInit, ViewChild, HostListener, Injector } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router, ActivatedRoute } from '@angular/router';
import { ScreenService } from 'src/app/api/controllers/Screen';
import { ModalService } from 'src/app/shared/services/modal.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';

@Component({
  selector: 'app-roles-and-permissions-list',
  templateUrl: './roles-and-permissions-list.component.html',
  styleUrls: ['./roles-and-permissions-list.component.scss']
})
export class RolesAndPermissionsListComponent implements OnInit {

  public roleHeader: Array<any> = [];
  public roleList: any;
  public isEnableCreate: boolean = true;
  modalService: any;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  @HostListener('window:navigateToPermission',['$event.detail'])
  navigateToPermission(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    this.router.navigate(['/ams/roles-permissions/set-permission'],{queryParams:{ secLevelId: dataRecord.secLevelId, roleId: dataRecord.roleId}});
  }
  @HostListener('window:onEditRole', ['$event.detail'])
  onEditRole(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    let menuRoleSecLevelId = dataRecord.menuRoleSecLevelId;
    this.router.navigateByUrl('/ams/roles-permissions/edit-roles-permissions/' + menuRoleSecLevelId);
  }
  @HostListener('window:onDeleteRole', ['$event.detail'])
  onDeleteRole(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let menuRoleSecLevelId = dataRecord.menuRoleSecLevelId
    this.modalService.showConfirmModal(menuRoleSecLevelId);
  }
  constructor(private router: Router,
    private screenService: ScreenService,
    private sharedService: SharedService,
    private sessionService: SessionService,
    private injector: Injector) {
    this.modalService = this.injector.get(ModalService);
  }

  ngOnInit(): void {
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.roleHeader = [{
      text: 'role type',
      datafield: 'roleId',
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Role Name',
      datafield: 'roleName',
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'set permission',
      cellsalign: 'center',
      align: 'center',
      width: 200,
      cellsrenderer: (row): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" onClick="navigateToPermission(' + row + ')" class="btn mat-primary">Set Permissions</a></div>'
      },
      renderer: columnrenderer
    },
    {
      text: 'action',
      cellsalign: 'center',
      align: 'center',
      width: 120,
      cellsrenderer: (row): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="onEditRole(' + row + ')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="onDeleteRole(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a></div>'
      },
      renderer: columnrenderer
    }];
    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe(id => {

      if (id != null) {

        var params = {
          menuRoleSecLevelId: id,
          deleteBy: parseInt(this.sessionService.userId)
        }

        this.screenService.deleteMenuRoleSecLevel(params).subscribe((res: any) => {
          this.getRolePermissionList();
        }, error => {
          console.log(error);
        });


      }

    })
    this.getRolePermissionList();
  }
  getRolePermissionList() {
    let param = {
      apartmentId: this.sessionService.apartmentId
    }
    this.screenService.getAllDefaultMenuRoleSecLevelbyApartmentId(param).subscribe(
      (resp: any) => {
        let refMenuData = resp.filter(data => {
          return data.isActive
        });
        let gridSourceData = {
          localdata: refMenuData,
          datatype: "array"

        }
        this.roleList = new jqx.dataAdapter(gridSourceData);
        this.isEnableCreate = true;
      }
    )
  }

}

function navigateToPermission(row) {
  var event = new CustomEvent('navigateToPermission', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).navigateToPermission = navigateToPermission;

function onEditRole(row) {
  var event = new CustomEvent('onEditRole', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).onEditRole = onEditRole;

function onDeleteRole(row) {
  var event = new CustomEvent('onDeleteRole', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).onDeleteRole = onDeleteRole;