import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/api/controllers/User';
import { ScreenService } from 'src/app/api/controllers/Screen';
import { Router, ActivatedRoute } from '@angular/router';
import { SessionService } from 'src/app/core/session/session.service';

@Component({
  selector: 'app-add-role',
  templateUrl: './add-role.component.html',
  styleUrls: ['./add-role.component.scss']
})
export class AddRoleComponent implements OnInit {

  allRoles: Array<any> = [];
  roleTypeId: any;
  roleName: any;
  description: any;
  menuRoleSecLevelId:any;
  isCreate:boolean=true;
  menuRole:any;
  constructor(private userService: UserService,
    private router: Router,
    private activatedRoute : ActivatedRoute,
    private sessionService : SessionService,
    private screenService: ScreenService) { }

  ngOnInit(): void {
    if (this.activatedRoute.params['value'].id != undefined) {
      this.menuRoleSecLevelId = this.activatedRoute.params['value'].id;
      this.isCreate = false;
      this.getRole();
    }
    this.getAllRoles();
  }

  getRole(){
      let queryParamBase = {
      apartmentId: this.sessionService.apartmentId,
      menuRoleSecLevelId: this.menuRoleSecLevelId,
    };
    this.screenService.getMenuRoleSecLevelbyRoleSecLevelId(queryParamBase).subscribe(
      resp =>{
        this.menuRole = resp[0];
        this.roleTypeId = this.menuRole.roleId;
        this.roleName = this.menuRole.roleName;
      }
    )
  }

  getAllRoles() {
    this.userService.getAllRoleTypes().subscribe(
      (resp: any) => {
        this.allRoles = resp;
        if (this.allRoles && this.allRoles.length) {
          this.allRoles.filter(key => {
            if (key.value == 2 || key.value == 4 || key.value == 5) {
              this.allRoles.splice(this.allRoles.indexOf(key), 1);
            }
          })
        }
      }
    )
  }

  addRole() {
    let addMenuLevelParam = {
      "menuSecLevelId": 0,
      "apartmentId": this.sessionService.apartmentId,
      "name": this.roleName,
      "isActive": true,
      "insertedBy": this.sessionService.userId,
      "insertedOn": new Date().toISOString(),
      "updatedBy": this.sessionService.userId,
      "updatedOn": "2020-08-09T06:22:21.539Z"
    }
    let secLevelParam = {
      seclevel: addMenuLevelParam
    }
    this.screenService.addMenuSecLevel(secLevelParam).subscribe(
      (resp:any) => {
        if (resp && resp.message) {
          let addMenuRoleSecLevel = {
            "menuRoleSecLevelId": 0,
            "apartmentId": this.sessionService.apartmentId,
            "roleId": parseInt(this.roleTypeId),
            "secLevelId": resp.message,
            "isActive": true,
            "insertedBy": this.sessionService.userId,
            "insertedOn": new Date().toISOString(),
            "updatedBy": this.sessionService.userId,
            "updatedOn": "2020-08-09T06:22:21.539Z"
          }
          let roleSecLevelParam = {
            roleseclevel: addMenuRoleSecLevel
          }
          this.screenService.addMenuRoleSecLevel(roleSecLevelParam).subscribe(
            resp => {
              this.router.navigate(['./']);
            }
          )
        }
      }
    )
  }

  updateRole(){
    let updataParam = {
      "menuSecLevelId": this.menuRole.menuRoleSecLevelId,
      "apartmentId":  this.sessionService.apartmentId,
      "name": this.roleName,
      "isActive": true,
      "insertedBy": 0,
      "insertedOn": new Date().toISOString(),
      "updatedBy": this.sessionService.userId,
      "updatedOn": new Date().toISOString()
    }
    let updateRoleParam = {
      menuSecLevel : updataParam
    }
    this.screenService.updateMenuSecLevel(updateRoleParam).subscribe(
      resp =>{
        this.router.navigate(['/ams/roles-permissions/role-permissions-list']);
      }
    )
  }

}
